<template>
  <div class="recommend">
    <div class="hot-recommend-box">
      <div class="hot-recommend">
        <div class="hot-text">
          <i class="hot-line"></i>   
          <span>热门推荐</span>
        </div>
        <div class="recommend-info clearfix">
          <div
            class="info-item fl"
            v-for="(item, index) in albumData.data.result"
            :key="index"
            :id="item.id"
            @click="viewMovieDetail({name: 'album', params: {id: item.id}})"
          >
            <div class="recommend-info-layer">
              <div class="fr">
                <span class="fl listen-icon"></span>
                <span class="fl listen-count">{{item.playCountes}}</span>
              </div>
            </div>
            <div class="info-img">
              <img class="auto-img" :src="item.picUrl" alt />
            </div>
            <div class="recommend-info-text">{{item.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("recommendModule");

export default {
  name: "recommend",
  data() {
    return {};
  },
  computed: {
    ...mapState(["albumData"])
  },

  methods: {
    //全部歌单
    viewMovieDetail(o) {
      this.$router.push(o);
    }
  },

  created() {
    // return
    //加载提示
    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });
    //请求数据
    this.axios({
      //请求类型
      method: "GET",

      //请求地址
      url: "http://www.arthurdon.top:3000/personalized"

      //请求参数
      // params: this.params
    })
    .then(result => {
      // console.log('result :', result);
      result.data.result.forEach(v => {
        v.playCountes = (v.playCount / 10000).toFixed(1) + "万";
      });
      
      this.$store.commit("recommendModule/changAlbumData", result);
      //取消加载提示
      this.$toast.clear();
    })
    .catch(err => {
      // console.log("err ==> ", err);
    });
  }
};
</script>

<style lang="less" scoped>
.recommend {
  margin: 0 10px;
  position: fixed;
  top: 98px;
  bottom: 120px;
  left: 0;
  right: 0;
  overflow: hidden;
  .hot-recommend-box {
    height: 100%;
    overflow-y: auto;
  }
  .hot-text {
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    position: relative;
    padding-left: 10px;
    span {
      line-height: 30px;
      display: block;
    }
  }
  .hot-line {
    width: 2px;
    height: 16px;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
  }

  .recommend-info {
    width: 100%;
    .info-item {
      width: calc(~"100% / 3 - 10px + 10px / 3");
      margin-right: 10px;
      margin-bottom: 10px;
      position: relative;
      top: 0;
      left: 0;
      &:nth-child(3n) {
        margin-right: 0;
      }
    }
    .recommend-info-layer {
      width: 100%;
      height: 24px;
      background-color: rgba(0, 0, 0, 0.5);
      position: absolute;
      top: 0;
      left: 0;
      line-height: 24px;

      .listen-icon {
        width: 16px;
        height: 16px;
        background: url("../../assets/icon/erji.png") no-repeat center center;
        background-size: 100% 100%;
        margin-top: 4px;
      }
      .listen-count {
        margin: 0 6px;
        font-size: 12px;
      }
    }
    .info-img {
      height: 112px;
      background-color: #fff;
    }
    .recommend-info-text {
      font-size: 12px;
      margin-top: 5px;
      height: 36px;
      line-height: 18px;
      overflow: hidden;
    }
  }
}
</style>